<template>
  <ContactForm :showHeader="false">

    <!--  BODY CONTENT  -->
    <div slot="body-content">
      <v-row style="margin-top:-7px" align="center" justify="center">
        <v-col :cols="choosedCategoryIsSet ? '11' : '12'">
          <v-autocomplete
            class="white"
            :rules="rules"
            v-model="setCategory"
            :color="outlineColor"
            :items="getCategories"
            outlined
            @change="categoryDocs"
            :label="$text('category')"
        ></v-autocomplete>
        </v-col>
        <v-col v-if="choosedCategoryIsSet" cols="1">
          <v-tooltip bottom>
            <template v-slot:activator="{ on }">
              <v-btn @click="extraInformationModal = true" v-on="on" class="more-info-btn" icon>
                <v-icon class="theme-bg-color">mdi-information-outline</v-icon>
              </v-btn>
            </template>
            <span>{{ $text('generalAnswer') }}</span>
          </v-tooltip>
        </v-col>
        <v-col cols="12">
          <v-text-field
            class="white text-field"
            v-model="setSubject"
            :color="outlineColor"
            outlined
            :label="$text('subject')"
            :rules="rules"
            hide-details="auto"
          ></v-text-field>
        </v-col>
        <v-col cols="12">
        <v-textarea
            auto-grow
            class="white"
            v-model="setMessage"
            :color="outlineColor"
            outlined
            :label="$text('message')"
            :rules="rules"
            hide-details="auto"
          ></v-textarea>
        </v-col>
      </v-row>

      <v-row class="px-4 pt-1 mt-2" align="center">
        <v-btn @click="toConfirm" :disabled="!sendBtnEnabled" depressed block class="theme-bg">
          {{ $text('nextBtn') }}
          <v-icon>mdi-menu-right</v-icon>
        </v-btn>
      </v-row>

      <v-dialog v-model="extraInformationModal" hide-overlay max-width="440">
        <v-card>
          <v-card-title style="margin-top:-8px" class="theme-bg justify-center align-center">
            {{ $text('generalAnswer') }}
          </v-card-title>
          <v-card-text class="body-2 secondary--text pt-5">
            <div class="template-msg-container py-5" v-html="getTemplateHtml" />
          </v-card-text>
          <v-card-actions style="margin-top:-20px">
            <v-btn @click="extraInformationModal = false" class="theme-bg-color" text block>
              Close
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>

    </div>
  </ContactForm>
</template>

<script>
import ContactForm from '@/components/ContactForm'
import { mapActions, mapState } from 'vuex'
import { editFormMessage } from '@/utils/helpers'

export default {
  name: 'Contact-page',
  components: {
    ContactForm
  },
  data: () => ({
    extraInformationModal: false,
    rules: [
      value => !!value || 'Required.',
      value => (value && value.length >= 3) || 'Min 3 characters'
    ],
    choosedCategory: {}
  }),
  computed: {
    ...mapState({
      config: ({ appState }) => appState.config,
      formData: ({ userData }) => userData.formData,
      textKeys: ({ appState }) => appState.textKeys,
      preFilledMsg: ({ userData }) => userData.preFilledMsg
    }),
    choosedCategoryIsSet () {
      return Object.keys(this.choosedCategory).length > 0
    },
    getTemplateHtml () {
      return this.choosedCategory.template
    },
    sendBtnEnabled () {
      const { formData } = this
      return formData.message.length > 3 && formData.category.length > 1 && formData.subject.length > 3
    },
    setSubject: {
      get: function () {
        return this.formData.subject
      },
      set: function (val) {
        this.setFormData({ subject: val })
      }
    },
    setCategory: {
      get: function () {
        return this.formData.category
      },
      set: function (val) {
        this.setFormData({ category: val })
      }
    },
    setMessage: {
      get: function () {
        return this.formData.message
      },
      set: function (val) {
        this.setFormData({ message: val })
      }
    },
    getCategories () {
      const result = []
      this.config.allCategories.forEach(val => {
        result.push({ text: val.category, type: 'Category', disabled: true })
        val.subCategories.forEach(el => {
          result.push({
            text: el.label,
            type: 'Subcategory',
            disabled: false,
            template: el.template,
            msg: el.message
          })
        })
      })
      return result
    },
    outlineColor () {
      return getComputedStyle(document.body).getPropertyValue('--theme-bg')
    }
  },
  methods: {
    ...mapActions([
      'setFormData',
      'setSendingMsg',
      'setTicketStatus'
    ]),
    setMsgWithCategory (categoryMsg, newMsg) {
      return editFormMessage({ categoryMsg, newMsg, message: this.setMessage })
    },
    replaceOldCategoryText () {
      let newMessage = this.setMessage
      this.getCategories.forEach(val => {
        if (newMessage.includes(val.msg)) {
          newMessage = newMessage.replace(val.msg, '')
        }
      })
      return newMessage
    },
    categoryDocs (category) {
      const newMessage = this.replaceOldCategoryText()
      const findCategory = this.getCategories.find(val => val.text === category)
      this.setMessage = this.setMsgWithCategory(findCategory.msg, newMessage)
      const { template } = findCategory || {}
      this.choosedCategory = {
        ...template && template.length > 0 && { template }
      }
    },
    toConfirm () {
      this.$router.push({ path: '/confirm', query: this.$route.query })
    }
  }
}
</script>

<style lang="scss" scoped>
.message-icon {
  position: absolute;
}
.more-info-btn {
  position: absolute;
  top: 30px;
  right: 15px;
}
</style>
